import common from'../../util/common.js'
import code from'../../util/code.js'
export default {
    data() {
        return {
			boxIndex: false, //收起还是展示
			radioScoreV:"",//单选分值
			checkScoreV:"",// 多选分值
			judgeScoreV:"",// 判断分值
			swiperIndex:1,//swiper 当前下标
			chuTime: '', //初始时间
			countDownV: '', //倒计时时间
			minute: '', //显示
			listNew: [], //合并后的数据
			userAnswers: [], //提交的答案
			currentHasChange:false,
			currentIndex:0,
			toptV:'',//单选题当前选项
			topDuo:[],//多选题当前选项
			markV:1,
			timeState:''
		}
    },
	onLoad() {
		var userId = uni.getStorageSync('userIdS');
		var examId = uni.getStorageSync('examIdS');
		if (userId == '' ||userId == undefined ||userId == null ||userId == NaN ||userId == 'NaN' ||
			examId == '' ||examId == undefined ||examId == null ||examId == NaN ||examId == 'NaN') {
			uni.reLaunch({
				url:'../index/index?mark =' + uni.getStorageSync("markS")
			});
			console.log('执行了');
			return;
		} else {
			this.radioScoreV = uni.getStorageSync('radioScoreS');
			this.checkScoreV = uni.getStorageSync('checkScoreS');
			this.judgeScoreV = uni.getStorageSync('judgeScoreS');
			var finalAnswer = uni.getStorageSync("finalResultS");
			var radioAnswerS = uni.getStorageSync("radioMarkS");
			var checkAnswerS = uni.getStorageSync("checkMarkS");
			var judgeAnswerS = uni.getStorageSync("judgeMarkS");
			var templateIdV = uni.getStorageSync("templateIdS"); 
			var mTime = uni.getStorageSync('minutsS');
			console.log(mTime);
			console.log(finalAnswer)
			if(uni.getStorageSync("durationS") == "-1"){
				this.timeState = false
			}else{
				this.timeState = true
				console.log(mTime);
				console.log(uni.getStorageSync('durationS'));
				if(finalAnswer != ""){
					if(mTime == ""){
						this.countDownV = uni.getStorageSync('durationS');
						console.log(this.countDown);
					}else{
						console.log("123")
						this.countDownV = mTime/60;
					}
				}else{
					this.countDownV = uni.getStorageSync('durationS');
					console.log(this.countDownV);
				}
				this.$nextTick(function() {
					this.countDown(this.countDownV*60);
				});
			}
			
			uni.setStorageSync('chuTime', code.UnixToDate(code.CurTime()))
			if (templateIdV == '' ||templateIdV == undefined ||templateIdV == null ||templateIdV == NaN ||templateIdV == 'NaN'){
				var data ={
					examId: examId
				}
			}else{
				var data ={
					examId: examId,
					templateId: templateIdV
				}
			}
			uni.request({
				url: common.port.mQuestion,
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				method: 'POST',
				dataType: 'json',
				data: data,
				success: result => {
					console.log(result.data);
					if (result.data.code == 500) {
						uni.showToast({
							title: result.data.msg,
							icon: 'none',
							duration: 2000
						});
						return;
					} else {
						uni.setStorageSync("templateIdS",result.data.templateId); 
						this.listNew = [...result.data.questions.radioQuestions, ...result.data.questions.checkQuestions,...result.data.questions.judgeQuestions];
						if (finalAnswer == "" || finalAnswer == undefined || finalAnswer == null || finalAnswer == NaN || finalAnswer == 'NaN') {
							for (var j = 0; j < this.listNew.length; j++) {
								this.userAnswers.push({
									questionId: this.listNew[j].id,
									questionType: this.listNew[j].type,
									userAnswer: '',
									markClass:'weido'
								});
							}
							uni.setStorageSync('finalResultS', this.userAnswers);
						}else{
							
							this.userAnswers = uni.getStorageSync('finalResultS');
							var that = this;
							console.log(this.swiperIndex);
							var indexData = finalAnswer[this.swiperIndex - 1];//根据下标找到相应的数据
							console.log(indexData);
							console.log(indexData.markClass)
							if(indexData.markClass.indexOf('biaodo') >-1){
								this.markV = 0;
							}else{
								this.markV = 1
							}
							if(indexData.userAnswer == ""){
									
							}else{
								console.log(indexData.questionType);
								if(indexData.questionType == 1){//单选题
									console.log(this.currentIndex);
									console.log(this.swiperIndex -1 )
									this.currentIndex = this.swiperIndex -1;
									console.log(indexData.userAnswer);
									this.toptV = indexData.userAnswer;
								}else if(indexData.questionType == 2){//多选题{
									console.log(this.currentIndex);
									console.log(this.swiperIndex -1 )
									this.currentIndex = this.swiperIndex -1;
									console.log(indexData.userAnswer);
									this.topDuo = indexData.userAnswer.split(",");
								}else if(indexData.questionType == 3){//判断题
									console.log(this.currentIndex);
									console.log(this.swiperIndex -1 )
									this.currentIndex = this.swiperIndex -1;
									console.log(indexData.userAnswer);
									this.toptV = indexData.userAnswer;
								}
							}
						}
					}
				}
			});
		}

	},
	// 计算
	computed:{
		radioBtn:function(){
			var radioList = []
			var userList = this.userAnswers;
			for (var value of userList) {
			  if(value.questionType == '1'){
				  radioList.push(value);
			  }
			}
			return radioList;
		},
		checkBtn:function(){
			var checkList = []
			var userList = this.userAnswers;
			for (var value of userList) {
			  if(value.questionType == '2'){
				  checkList.push(value);
			  }
			}
			return checkList;
			
		},
		judgeBtn:function(){
			var judgeList = []
			var userList = this.userAnswers;
			for (var value of userList) {
			  if(value.questionType == '3'){
				  judgeList.push(value);
			  }
			}
			return judgeList;
		}
	},
    methods: {
		swiperChange(e){ //swiper滑动
			this.swiperIndex = e.detail.current + 1; //当前题
			console.log(this.userAnswers);
			var finalAnswer = uni.getStorageSync("finalResultS");
			console.log(finalAnswer)
			if (finalAnswer == "" || finalAnswer == undefined || finalAnswer == null || finalAnswer == NaN || finalAnswer == 'NaN') {
				for (var j = 0; j < this.listNew.length; j++) {
					this.userAnswers.push({
						questionId: this.listNew[j].id,
						questionType: this.listNew[j].type,
						userAnswer: '',
						markClass:'weido'
					});
				}
				uni.setStorageSync('finalResultS', this.userAnswers);
			}else{
				this.userAnswers = uni.getStorageSync('finalResultS');
