						console.log(this.swiperIndex -1 )
						this.currentIndex = this.swiperIndex -1;
						console.log(indexData.userAnswer);
						this.topDuo = indexData.userAnswer.split(",");
					}else if(indexData.questionType == 3){//判断题
						console.log(this.currentIndex);
						console.log(this.swiperIndex -1 )
						this.currentIndex = this.swiperIndex -1;
						console.log(indexData.userAnswer);
						this.toptV = indexData.userAnswer;
					}
				}
			}
		},
		// 标记
