			}
		},
		// 下一题
		down(){
			if(this.swiperIndex + 1 <= this.listNew.length){
				this.swiperIndex = this.swiperIndex + 1; //当前题
				var finalAnswer = uni.getStorageSync("finalResultS");
				console.log(finalAnswer)
				if (finalAnswer == "" || finalAnswer == undefined || finalAnswer == null || finalAnswer == NaN || finalAnswer == 'NaN') {
					for (var j = 0; j < this.listNew.length; j++) {
						this.userAnswers.push({
							questionId: this.listNew[j].id,
							questionType: this.listNew[j].type,
							userAnswer: '',
							markClass:'weido'
						});
					}
					uni.setStorageSync('finalResultS', this.userAnswers);
				}else{
					this.userAnswers = uni.getStorageSync('finalResultS');
					var that = this;
					this.swiperIndex;
					console.log(this.swiperIndex);
					var indexData = finalAnswer[this.swiperIndex - 1];//根据下标找到相应的数据
					console.log(indexData);
					if(indexData.markClass.indexOf('biaodo') >-1){
						this.markV = 0;
					}else{
						this.markV = 1
					}
					if(indexData.userAnswer == ""){

					}else{
						
						console.log(indexData.questionType);
						if(indexData.questionType == 1){//单选题
							console.log(this.currentIndex);
							console.log(this.swiperIndex -1 )
							this.currentIndex = this.swiperIndex -1;
							console.log(indexData.userAnswer);
							this.toptV = indexData.userAnswer;
						}else if(indexData.questionType == 2){//多选题{
							console.log(this.currentIndex);
							console.log(this.swiperIndex -1 )
							this.currentIndex = this.swiperIndex -1;
							console.log(indexData.userAnswer);
							this.topDuo = indexData.userAnswer.split(",");
						}else if(indexData.questionType == 3){//判断题
							console.log(this.currentIndex);
							console.log(this.swiperIndex -1 )
							this.currentIndex = this.swiperIndex -1;
							console.log(indexData.userAnswer);
							this.toptV = indexData.userAnswer;
						}
					}
				}
			}else{
				uni.showToast({
					title: '最后一题',
					icon: 'none',
					duration: 2000
				})
			}
		},
		// 交卷
		showModal(){
			var nullNum = [];
			this.userAnswers.forEach( ( nullList, i ) => {
				if(nullList.userAnswer == ""){
					nullNum.push(i+1);
				}
			} );
			console.log(nullNum)
			var cc = nullNum.join(",")
			console.log(cc)
			var topicAnswer = this.userAnswers;
			for(var i= 0 ;i<topicAnswer.length;i++){
				delete topicAnswer[i].markClass;
			}
			console.log(topicAnswer);
			var bc = ""
			if(cc.length>0){
				bc = '第'+ cc + '题没做，确定要交卷吗'
			}else{
				bc ='确定要交卷吗'
			}
			uni.showModal({
				title: '提交试卷',
				content: bc,
				success: function (res) {
					if (res.confirm) {
						console.log('用户点击确定');
						uni.showLoading({
							title: '加载中...',
						})
						var examIdV = uni.getStorageSync("examIdS");
						var userId = uni.getStorageSync("userIdS");
						uni.request({
							url: common.port.examSubmit,
							header: {
								'content-type': 'application/json',
							},
							method: "POST",
							dataType: "json",
							data:{
								"userId":userId,
								"examId":examIdV,
								"startTime":uni.getStorageSync('chuTime'),
								"userAnswers":topicAnswer
							},
							success: (result) => {
								uni.hideLoading();
								console.log(result.data);
								if(result.data.code == 500){
									uni.showToast({
										title: '请重新提交试卷',
										icon: 'none',
										duration: 2000
									})
									return
								}else{
									uni.setStorageSync("answerIdS",result.data.answer_id)
									uni.setStorageSync("minutsS","0")
									uni.reLaunch({
										url:'../paper/paper'
									})
									
								}
							}
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			},)
		},
		//显示隐藏题数
		showFixed(){
			console.log(this.boxIndex);
			if (this.boxIndex == false) {
				this.boxIndex = true;
				console.log(this.boxIndex);
			} else {
				this.boxIndex = false;
			}
		},
		// 点击选项事件
		radioChange(event,topic,topt,type,id){//topic当前题,topt,当前选项
			let that = this;
			console.log(topic)
			console.log(that.userAnswers);
			var bData = that.userAnswers[topic];//根据下标找到相应的数据
			console.log(bData);
			if(type == 1){//单选题
				that.currentIndex= this.swiperIndex - 1;
				that.toptV = topt;
				if(id == bData.questionId){
					bData.userAnswer = topt;
				}else{
					this.userAnswers.forEach(function (element, index) {
						console.log(element);
						if(id == element.questionId){
							bData.userAnswer = topt
						}else{
							return;
						}
					});
				}
				bData.markClass = "yido";
				uni.setStorageSync('finalResultS', this.userAnswers);
				console.log(this.userAnswers)
				console.log(uni.getStorageSync("finalResultS"));
			}else if(type == 2){//多选题
				let conDuo = "";
				if(this.currentIndex == topic){
					if(that.topDuo.indexOf(topt)>-1){//存在
						that.topDuo.splice(that.topDuo.indexOf(topt),1)
					}else{
						that.topDuo.push(topt);
					}
					if(id == bData.questionId){
						bData.userAnswer = that.topDuo.sort().join(",");
					}else{
						this.userAnswers.forEach(function (element, index) {
							console.log(element);
							if(id == element.questionId){
								bData.userAnswer = that.topDuo.sort().join(",");
							}else{
								return;
							}
						});
					}
				}else{
					that.currentIndex= this.swiperIndex - 1;
					that.topDuo =[];
					if(that.topDuo.indexOf(topt)>-1){//存在
						that.topDuo.splice(that.topDuo.indexOf(topt),1)
					}else{
						that.topDuo.push(topt);
					}
					if(id == bData.questionId){
						bData.userAnswer = that.topDuo.sort().join(",");
					}else{
						this.userAnswers.forEach(function (element, index) {
							console.log(element);
							if(id == element.questionId){
								bData.userAnswer = that.topDuo.sort().join(",");
							}else{
								return;
							}
						});
					}
				}
				console.log(this.topDuo)
				bData.markClass = "yido";
				uni.setStorageSync('finalResultS', this.userAnswers);
				console.log(uni.getStorageSync("finalResultS"));
			}else if(type == 3){//判断
				that.currentIndex= this.swiperIndex - 1;
				that.toptV = topt;
				if(id == bData.questionId){
					bData.userAnswer = topt;
				}else{
					this.userAnswers.forEach(function (element, index) {
						console.log(element);
						if(id == element.questionId){
							bData.userAnswer = topt
						}else{
							return;
						}
					});
				}
				bData.markClass = "yido";
				uni.setStorageSync('finalResultS', this.userAnswers);
			}
			console.log(uni.getStorageSync('finalResultS'))

		},
		//倒计时
		countDown(time) {
			console.log(time);
			this.interval = setInterval(() => {
				if (time == 0) {
					console.log("执行等于0");
					clearInterval(this.interval);
					var topicAnswer = this.userAnswers;
					for(var i= 0 ;i<topicAnswer.length;i++){
						delete topicAnswer[i].markClass;
					}
					console.log(topicAnswer);
					var examIdV = uni.getStorageSync("examIdS");
					var userId = uni.getStorageSync("userIdS");
					uni.request({
						url: common.port.examSubmit,
						header: {
							'content-type': 'application/json',
						},
						method: "POST",
						dataType: "json",
						data:{
							"userId":userId,
							"examId":examIdV,
							"startTime":uni.getStorageSync('chuTime'),
							"userAnswers":topicAnswer
						},
						success: (result) => {
							console.log(result.data);
							if(result.data.code == 500){
								uni.showToast({
									title: "请重新提交试卷",
									icon: 'none',
									duration: 2000
								})
								return
							}else{
								uni.setStorageSync("answerIdS",result.data.answer_id);
								uni.reLaunch({
									url:'../paper/paper'
								})
								
							}
						}
					})
					
				}else if(time > 0){
					console.log("执行大约0");
					console.log(time);
					time = time - 1;
					// console.log(time);
					var minuteV = parseInt(time / 60);
					var secondV = parseInt(time % 60);
					this.minute = minuteV + '分' + secondV + '秒';
					uni.setStorageSync("minutsS",time)
				}
			}, 1000);
		},
		//跳转
		skip(index){
			this.swiperIndex = index;
			setTimeout(() => {
				this.boxIndex = false
			}, 1000);

		}
	},
}
